<script>
import axios from "axios";
export default {
  props: ['id'],
  data() {
    return {
      movie: {},
      country: String,
      adult: Boolean,
      backdrop_path: String,
      genre_ids: Array,
      original_language: String,
      original_title: String,
      overview: String,
      popularity: Number,
      poster_path: String,
      release_date: String,
      date: String,
      title: String,
      video: Number,
      vote_average: Number,
      displayRating: Number,
      vote_count: Number,
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    }
  },
  async mounted() {
    const response = await axios.get("/api/movies/" + this.id);
    this.movie = await response.data;
    this.country = await this.movie[0].country;
    this.adult = await this.movie[0].adult;
    this.backdrop_path = await this.movie[0].backdrop_path;
    this.genre_ids = await this.movie[0].genre_ids;
    this.original_language = await this.movie[0].original_language;
    this.original_title = await this.movie[0].original_title;
    this.overview = await this.movie[0].overview;
    this.popularity = await this.movie[0].popularity;
    this.poster_path = await this.movie[0].poster_path;
    this.release_date = await this.movie[0].release_date;
    this.title = await this.movie[0].title;
    this.video = await this.movie[0].video;
    this.vote_average = await this.movie[0].vote_average;
    this.vote_count = await this.movie[0].vote_count;

    this.displayRating = String("linear-gradient(90deg,#08ae86 " + (this.vote_average/10)*100 + "%,transparent " + (((this.vote_average/10)*100)+1) + "%)"); 

    var dateObj = new Date(this.release_date);
    var month = dateObj.getUTCMonth() + 1;
    var day = dateObj.getUTCDate();
    var year = dateObj.getUTCFullYear();

    this.date = this.months[month-1] + " " + day + ", " + year;

  },
}

</script>

<template>
  <div class="result-scroll">
    <div class="result-details">
      <h2>{{ title }}</h2>

      <img alt="five star image" v-if="vote_average != 0" class="book-rating" src = "../../../assets/ten-stars.png" :style="{background: displayRating}"/>
    
      <p v-if="vote_average != 0"><b>Rating: </b> {{ vote_average }} out of 10</p>
      <p>{{ date }}</p>

      <img :alt="title" class="movieImage" v-if="backdrop_path != null" :src="'https://image.tmdb.org/t/p/w600_and_h900_bestv2/' + backdrop_path"/>

      <img :alt="title" class="movieImage" v-if="backdrop_path == null && poster_path != null" :src="'https://image.tmdb.org/t/p/w600_and_h900_bestv2/'+ poster_path"/>
      
      <p><b>Vote Count: </b> {{ vote_count }}</p>
    
      <a v-bind:href="'https://www.themoviedb.org/movie/'+ this.id" target="_blank" class="button">View Movie</a>
    
      <p>
        {{genres}}
      </p>
    
      <p v-if="typeof audio_length_sec != 'function'">{{ original_title }}</p>
      <p v-if="typeof overview != 'function'">{{ overview }}</p>

    </div>
  </div>
</template>


